.header__title {
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  max-width: calc(100% - 140px);
  transform: translate(-50%, -50%);
  text-align: center;
  font-size: 24px;
  line-height: 26px;
  font-style: normal;
  font-weight: bold;
  color: #059;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

@media screen and (min-width: 425px) {
  .header__title {
    font-size: 30px;
    line-height: 34px;
  }
}
